"use client";

import { Button } from "@/components/ui/button";
import { cn } from "@/lib/utils";
import type { ScoreType } from "@/types/album";
import { ScoreType as ScoreEnum } from "@/types/album";

interface FilterButtonsProps {
  selectedFilter: ScoreType | null;
  onFilterSelect: (filter: ScoreType | null) => void;
}

const filterButtons = [
  { filter: ScoreEnum.CLASSIC, label: "CLASSIC" },
  { filter: ScoreEnum.TEN, label: "10/10" },
  { filter: ScoreEnum.NINE, label: "9/10" },
  { filter: ScoreEnum.EIGHT, label: "8/10" },
  { filter: ScoreEnum.SEVEN, label: "7/10" },
  { filter: ScoreEnum.SIX, label: "6/10" },
  { filter: ScoreEnum.FIVE, label: "5/10" },
  { filter: ScoreEnum.FOUR, label: "4/10" },
  { filter: ScoreEnum.THREE, label: "3/10" },
  { filter: ScoreEnum.TWO, label: "2/10" },
  { filter: ScoreEnum.ONE, label: "1/10" },
  { filter: ScoreEnum.ZERO, label: "0/10" },
  { filter: ScoreEnum.NOT_GOOD, label: "NOT GOOD" },
  { filter: ScoreEnum.OTHER_SCORES, label: "OTHER SCORES" },
];

export function FilterButtons({
  selectedFilter,
  onFilterSelect,
}: FilterButtonsProps) {
  return (
    <div className="flex flex-wrap gap-2 mb-4">
      {filterButtons.map(({ filter, label }) => (
        <Button
          key={filter}
          variant="outline"
          className={cn(
            "text-sm",
            selectedFilter === filter
              ? "bg-blue-600 text-white hover:bg-blue-700"
              : "bg-gray-800 text-gray-300 hover:bg-gray-700"
          )}
          onClick={() => {
            onFilterSelect(filter === selectedFilter ? null : filter);
            return console.log("Selected Filter : ", filter);
          }}
        >
          {label}
        </Button>
      ))}
    </div>
  );
}
